.about-title {
  margin-bottom: 4rem;
  font-size: 4.2rem;
  font-weight: 700;
}
.my-card {
  user-select: none;
  padding: 1rem;
  box-shadow: none;
  transition: all 0.4s ease-in;
  border-radius: 10px;
}
.my-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  z-index: 10;
}
.my-card-title {
  font-weight: 700;
  font-size: 1.5rem;
  font-weight: 700;
}
.my-link-git,
.my-link-ldn {
  font-size: 3rem;
  padding: 10px;
}
.my-link-git {
  color: #495057;
}
.my-link-git:hover,
.my-link-git:active {
  color: #212529 !important;
}
.my-btn {
  margin-top: 0.5rem;
  animation: e 53s linear infinite;
  background: linear-gradient(
      121.19deg,
      rgba(133, 239, 49, 0) 25.73%,
      hsla(0, 0%, 100%, 0.3) 45.27%,
      rgba(133, 239, 49, 0) 62.27%
    ),
    #ff8d1a;
}

.my-btn,
.my-btn:active,
.my-btn:hover {
  border: none;
  outline: none;
}

.my-btn:hover {
  background-color: #222;
}
.my-btn:active {
  background-color: #000;
}

@keyframes e {
  0% {
    background-position: -5000px 0;
  }
  100% {
    background-position: 5000px 0;
  }
}
