@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Rock+Salt&display=swap);
*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
html,
body {
  overflow-x: hidden;
  background: linear-gradient(0deg, #ffdee9, #b5fffc) no-repeat;
}
#header-font {
  font-family: "Rock Salt", cursive;
  font-size: 2rem;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(255, 239, 150);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: goldenrod;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(250, 182, 10);
}

#loader {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.main-container {
  width: 100vw;
  height: 85.5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
@media (max-width: 768px) {
  .nav-md {
    margin-top: 16px;
  }
}

/* .home {
  width: 70rem;
} */
.home {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.neonText {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #80d0c7,
    0 0 82px #80d0c7, 0 0 92px #80d0c7, 0 0 102px #80d0c7, 0 0 151px #80d0c7;
}

.name {
  font-size: 4.2rem;
  font-weight: 700;
  -webkit-animation: flicker 1.5s infinite alternate;
          animation: flicker 1.5s infinite alternate;
}

/* Flickering animation */
@-webkit-keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #80d0c7,
      0 0 80px #80d0c7, 0 0 90px #80d0c7, 0 0 100px #80d0c7, 0 0 150px #80d0c7;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}
@keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #80d0c7,
      0 0 80px #80d0c7, 0 0 90px #80d0c7, 0 0 100px #80d0c7, 0 0 150px #80d0c7;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}
.name {
  transition: all 0.4s ease-out;
  margin-bottom: 4rem;
}
.name:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
#typewriter-start {
  font-size: 1.5rem;
  color: goldenrod;
  font-weight: 400;
}
#typewriter-end {
  font-size: 1.5rem;
  display: inline-block;
}
.Typewriter__cursor {
  font-weight: 700;
  color: goldenrod;
}

.about-title {
  margin-bottom: 4rem;
  font-size: 4.2rem;
  font-weight: 700;
}
.my-card {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 1rem;
  box-shadow: none;
  transition: all 0.4s ease-in;
  border-radius: 10px;
}
.my-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  z-index: 10;
}
.my-card-title {
  font-weight: 700;
  font-size: 1.5rem;
  font-weight: 700;
}
.my-link-git,
.my-link-ldn {
  font-size: 3rem;
  padding: 10px;
}
.my-link-git {
  color: #495057;
}
.my-link-git:hover,
.my-link-git:active {
  color: #212529 !important;
}
.my-btn {
  margin-top: 0.5rem;
  -webkit-animation: e 53s linear infinite;
          animation: e 53s linear infinite;
  background: linear-gradient(
      121.19deg,
      rgba(133, 239, 49, 0) 25.73%,
      hsla(0, 0%, 100%, 0.3) 45.27%,
      rgba(133, 239, 49, 0) 62.27%
    ),
    #ff8d1a;
}

.my-btn,
.my-btn:active,
.my-btn:hover {
  border: none;
  outline: none;
}

.my-btn:hover {
  background-color: #222;
}
.my-btn:active {
  background-color: #000;
}

@-webkit-keyframes e {
  0% {
    background-position: -5000px 0;
  }
  100% {
    background-position: 5000px 0;
  }
}

@keyframes e {
  0% {
    background-position: -5000px 0;
  }
  100% {
    background-position: 5000px 0;
  }
}



