@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Rock+Salt&display=swap");
*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
html,
body {
  overflow-x: hidden;
  background: linear-gradient(0deg, #ffdee9, #b5fffc) no-repeat;
}
#header-font {
  font-family: "Rock Salt", cursive;
  font-size: 2rem;
}
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(255, 239, 150);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: goldenrod;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(250, 182, 10);
}
