.home {
  user-select: none;
}
.neonText {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #80d0c7,
    0 0 82px #80d0c7, 0 0 92px #80d0c7, 0 0 102px #80d0c7, 0 0 151px #80d0c7;
}

.name {
  font-size: 4.2rem;
  font-weight: 700;
  animation: flicker 1.5s infinite alternate;
}

/* Flickering animation */
@keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #80d0c7,
      0 0 80px #80d0c7, 0 0 90px #80d0c7, 0 0 100px #80d0c7, 0 0 150px #80d0c7;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}
.name {
  transition: all 0.4s ease-out;
  margin-bottom: 4rem;
}
.name:hover {
  transform: scale(1.1);
}
#typewriter-start {
  font-size: 1.5rem;
  color: goldenrod;
  font-weight: 400;
}
#typewriter-end {
  font-size: 1.5rem;
  display: inline-block;
}
.Typewriter__cursor {
  font-weight: 700;
  color: goldenrod;
}
